@import "../../../node_modules/slick-carousel/slick/slick.scss";

.slick-dots {
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    column-gap: 18px;
    line-height: 0;

    button {
        padding: 0;
        border: none;
        background-color: #D9D9D9;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        cursor: pointer;
        font-size: 0;

        &:hover,
        &:focus {
            background: var(--olive-color);
        }
    }
}

.slick-active button {
    background-color: var(--olive-color);
}